import React, {useState} from 'react';
import styled from 'styled-components';
import MenuIcon from '@material-ui/icons/Menu'
import logo from '../img/logo.svg';


const Navb = () => {
  const [showLinks, setShowLinks] = useState(false);
  return(
      <Navbar bg="light" variant="light">
        <LeftSide>
          <h1 data-text="NKANDA">NKANDA</h1>
        </LeftSide>
        <RightSide>
        <BurgerButton onClick={() => setShowLinks(!showLinks)}><MenuIcon className="bb"/></BurgerButton>
          <Links id={showLinks ? "hidden" : ""}>
          <div class="container1">
          <a  href="#" class="button">Home</a>
            <div class="drip-1"></div>
            <div class="drip-2"></div>
            <div class="drip-3"></div>
            </div>
            <div class="container1">
            <a  href="#about" class="button">About</a>
            <div class="drip-1"></div>
            <div class="drip-2"></div>
            <div class="drip-3"></div>
            </div>
            <div class="container1">
            <a  href="#projects" class="button">Projects</a>
            <div class="drip-1"></div>
            <div class="drip-2"></div>
            <div class="drip-3"></div>
            </div>
          </Links>
        </RightSide>
      </Navbar>
    )
};

export const Navbar = styled.div`
    display: flex;
    width: 100%;
    height: 100px;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    justify-content: space-between;
    padding: 1.5rem 11vw;
    @media(max-width: 750px){ padding: 1.5rem 1rem; height: 100px;}
    h1{
      color:#7510F7;
      font-size: 3.2rem;
      margin: 0;
    }
`;

export const BurgerButton = styled.button`
  border: none;
  background-color: Transparent;
  .bb{
    width: 90px;
    height: 40px;
  }
`;
export const LeftSide = styled.div`
    h1 {
          font-size: 2rem;
          color: transparent;
          -webkit-text-stroke: 2px #36D8C5;
          position: relative;
        } 
        h1::before{
          content: attr(data-text);
          color: #36D8C5;
          position: absolute;
          animation: animate 3s linear infinite;
        }
        @keyframes animate {
          0%, 100%{
            clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 
              54% 60%, 70% 61%, 84% 59%, 100% 52%, 100% 100%,
              0% 100%);
          }
          50%{
            clip-path: polygon(0% 60%, 16% 65%, 34% 66%, 
              51% 62%, 67% 50%, 84% 45%, 100% 46%, 100% 100%,
              0% 100%);
          }
          }
        
          @media only screen and (max-width: 750px) {
      h1{
        font-size: 2rem;
      }
    }
`;
export const RightSide = styled.div`
    display: flex;
    button{
      display: none;
    }
    @media only screen and (max-width: 650px) {
      button{
        display:flex;
      }
      a{
        display: none;
      }
      #hidden{
        position: absolute;
        left: 0px;
        top: 90px;
        height: 125px;
        width: 100%;
        background-color: white;
        box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px;
        display: flex;
        flex-direction: column;
        align-items: center;
        max-height: 200px;
      }
    }
    #hidden{
      display: flex;
    }
    #hidden a{
      display: flex;
      margin: 1px;
    }
`;
export const Links = styled.div`
    max-height: 80px;
    font-size: 25px;
    display: flex;
    a{
      margin-left: 15px;
      color: #2b2b2b;
      cursor: pointer;
      text-decoration: none;
    }
    @media only screen and (max-width: 750px) {
      a{
        font-size: 20px;
      }
    }
`;

export default Navb;